import Vue from 'vue'
import App from './App'
import router from './router/router'
import HelloWorld from '@/components/HelloWorld'
import Tictacto from '@/components/Tictacto'

Vue.config.productionTip = false

/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  components: { App },
  template: '<App/>'
})


